/* src/components/SuccessStories.css */

.success-stories-container {
    padding: 20px;
    background-color: #f5f5f5; /* Light grey background */
    min-height: 100vh; /* Full viewport height */
}

.story-cards {
    display: flex;
    flex-wrap: wrap; /* Allow cards to wrap to the next line */
    justify-content: center; /* Center the cards */
    gap: 20px; /* Space between cards */
}

.story-card {
    background-color: white; /* Card background */
    border-radius: 15px; /* Rounded corners */
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2); /* Shadow effect */
    padding: 15px; /* Padding inside the card */
    width: 300px; /* Fixed width for cards */
    max-width: 100%; /* Ensure cards don't exceed their container */
    
}

.story-card:hover {
    box-shadow: 0 6px 12px rgba(0, 0, 0, 0.3); /* Increase shadow on hover */
}

.story-image {
    width: 100%; /* Responsive image */
    border-radius: 10px; /* Round image corners */
}

.story-title {
    color: #333; /* Darker title color */
    text-align: center; /* Center align title */
    font-size: 1.5em; /* Title font size */
    margin: 10px 0; /* Margin around title */
}

.story-description {
    color: #555; /* Softer text color */
    margin: 5px 0; /* Margin around text */
}

strong {
    color: #333; /* Darker color for strong text */
}
